@font-face {
  font-family: "IBM Plex Sans";
  src: local("IBM Plex Sans"),
    local("IBMPlexSans"),
    url("../fonts/font-new/IBMPlexSans-Regular.woff2") format("woff2"),
    url("../fonts/font-new/IBMPlexSans-Regular.woff") format("woff"),
    url("../fonts/font-new/IBMPlexSans-Regular.ttf") format("truetype");
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "IBM Plex Sans";
  src: local("IBM Plex Sans  Italic"),
    local("IBMPlexSans-Italic"),
    url("../fonts/font-new/IBMPlexSans-Italic.woff2") format("woff2"),
    url("../fonts/font-new/IBMPlexSans-Italic.woff") format("woff"),
    url("../fonts/font-new/IBMPlexSans-Italic.ttf") format("truetype");
  font-weight: 400;
  font-style: italic;
  font-display: swap;
}

@font-face {
  font-family: "IBM Plex Sans";
  src: local("IBM Plex Sans Bold"),
    local("IBMPlexSans-Bold"),
    url("../fonts/font-new/IBMPlexSans-Bold.woff2") format("woff2"),
    url("../fonts/font-new/IBMPlexSans-Bold.woff") format("woff"),
    url("../fonts/font-new/IBMPlexSans-Bold.ttf") format("truetype");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "IBM Plex Sans";
  src: local("IBM Plex Sans Medium"),
  local("IBMPlexSans-Medium"),
  url("../fonts/font-new/IBMPlexSans-Medium.woff2") format("woff2"),
  url("../fonts/font-new/IBMPlexSans-Medium.woff") format("woff"),
  url("../fonts/font-new/IBMPlexSans-Medium.ttf") format("truetype");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
